import React, { Component } from 'react'
import app_icon from "../static/./Icon - Sehat Easy.png"
import {Link} from 'react-router-dom'

export default class Navbar extends Component {
    render() {
        return (
          <nav className="navbar navbar-expand-custom navbar-mainbg">
        <Link className="navbar-brand navbar-logo" to="/"><img src={app_icon} alt=""/></Link>
        <button className="navbar-toggler" type="button" aria-controls="navbarSupportedContent" aria-expanded="false"
            aria-label="Toggle navigation">
            <i className="fas fa-bars text-black"></i>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto">
                <div className="hori-selector">
                    <div className="left"></div>
                    <div className="right"></div>
                </div>
                <li className="nav-item">
                    <Link className="nav-link" to="/"><i className="fas fa-home"></i>Home</Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/service"><i className="far fa-address-book"></i>Services</Link>
                </li>
                <li className="nav-item active">
                    <Link className="nav-link" to="/about"><i className="far fa-clone"></i>About Us</Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/contact"><i className="far fa-id-card"></i>Contact Us</Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/faq"><i className="fas fa-question"></i>Faq</Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/login"><i className="far fa-user"></i>Login</Link>
                </li>
            </ul>
        </div>
    </nav>
        )
    }
}
