import React, { Component } from 'react'

export default class Contact extends Component {
    render() {
        return (
            <section id="contact">
            <div class="background">
                <div class="form">
                    <div class="screen">
                        <div class="screen-header">
                            <div class="screen-header-left">
                                <i class="fas fa-circle"></i>
                                <i class="fas fa-circle"></i>
                                <i class="fas fa-circle"></i>
                            </div>
                            <div class="screen-header-right">
                                <h3>.</h3>
                                <h3>.</h3>
                                <h3>.</h3>
                            </div>
                        </div>
                        <div class="screen-body">
                            <div class="screen-body-item left">
                                <div class="app-title">
                                    <span>CONTACT</span>
                                    <span>US</span>
                                </div>
                                <div class="app-contact">CONTACT INFO : +91 860-582-7905</div>
                            </div>
                            <div class="screen-body-item">
                                <div class="app-form">
                                    <div class="app-form-group">
                                        <input type="text" class="app-form-control" name="name" placeholder="NAME"/>
                                    </div>
                                    <div class="app-form-group">
                                        <input type="email" class="app-form-control" name="email" placeholder="EMAIL"/>
                                    </div>
                                    <div class="app-form-group">
                                        <input type="tel" class="app-form-control" name="phone" placeholder="CONTACT NO"/>
                                    </div>
                                    <div class="app-form-group message">
                                        <input type="text" class="app-form-control" name="text" placeholder="MESSAGE"/>
                                    </div>
                                    <div class="app-form-group buttons">
                                        <button class="app-form-button">SEND</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                </div>
        </section>

        )
    }
}
