import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class Footer extends Component {
    render() {
        return (
        <section className="footer">
            <div className="footer__addr">
            <h1 className="footer__logo"><span className="safrron">sehat</span><span
                    className="green">Easy</span></h1>
            <h2>Contact</h2>

            <address>
                Sehat Easy, Bajaj Incubation Center, Dr BAMU campus, Aurangabad.<br/>

                <Link className="footer__btn" to="info@sehateasy.com">Email Us</Link>
            </address>
        </div>

            <ul className="footer__nav">
            <li className="nav__item">
                <h2 className="nav__title"> Social Media</h2>

                <ul className="nav__ul">
                    <li>
                        <Link to="https://www.facebook.com/SehatEasy"><i className="fab fa-facebook"></i> Facebook</Link>
                    </li>

                    <li>
                        <Link to="https://twitter.com/SehatEasy"><i className="fab fa-twitter"></i> Twitter</Link>
                    </li>

                    <li>
                        <Link to="https://www.instagram.com/sehateasy/"><i className="fab fa-instagram"></i> Instagram</Link>
                    </li>

                    <li>
                        <Link to="https://www.linkedin.com/company/sehateasycom"><i className="fab fa-linkedin"></i>
                            LinkedIn</Link>
                    </li>

                    <li>
                        <Link to="https://api.whatsapp.com/send?phone=918605827905"><i className="fab fa-whatsapp"></i>
                            WhatsApp</Link>
                    </li>
                </ul>
            </li>

            <li className="nav__item nav__item--extra">
                <h2 className="nav__title">Services</h2>

                <ul className="nav__ul nav__ul--extra">
                    <li>
                        <Link to="#"><i className="fas fa-pills"></i> Find Medicine Shop</Link>
                    </li>

                    <li>
                        <Link to="#"><i className="fas fa-user-md"></i> Get Doctor Consult</Link>
                    </li>

                    <li>
                        <Link to="#"><i className="fas fa-ambulance"></i> Emergency Services</Link>
                    </li>

                    <li>
                        <Link to="#"><i className="fas fa-hospital"></i> Hospitals</Link>
                    </li>

                    <li>
                        <Link to="#"><i className="fas fa-microscope"></i> Diagnostic Centers</Link>
                    </li>
                    <li>
                        <Link to="#"><i className="fas fa-lock"></i> Privacy Policy</Link>
                    </li>
                </ul>
            </li>

        </ul>
        <div className="termsConditions">
            <Link to="/terms/terms of use.html">Terms of Use</Link>
            <Link to="/terms/privacystatement.html">Privacy Statement</Link>
        </div>
          <div className="copyright">
            <p>&copy; SehatEasy 2021-2022</p>
        </div>
          
        </section >
        )
    }
}
